import { AfterViewInit, Component, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { NgxSmartModalComponent, NgxSmartModalService } from 'ngx-smart-modal';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';
import { ToastrService } from 'ngx-toastr';
import { UserStateService } from '../../blocks/services/user-state.service';
import { ApiResult } from '../../blocks/services/models';
import {
  DocumentLimits,
  TenantDetails,
  TenantOptions,
  PasswordPolicy,
  MeasurementSettings,
  MeasurementUnit,
  Feature,
} from 'shared-code-lib';

@Component({
  selector: 'app-admin-tenant-options',
  templateUrl: './admin-tenant-options.component.html',
  styleUrls: ['./admin-tenant-options.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminTenantOptionsComponent implements OnInit, AfterViewInit {
  tenantDetails: TenantDetails;
  optionsErrors: string[] = [];
  documentLimitsErrors: string[] = [];
  passwordPolicyErrors: string[] = [];
  measurementSettingsErrors: string[] = [];
  tenantoptionsdocumentlimitsModalClosed: boolean = true;
  expireUnits: number;
  showDocumentLimits = false;
  showMeasurementSettings = false;
  measurementUnit = MeasurementUnit;

  authenticationModal: NgxSmartModalComponent;
  documentLimitsModal: NgxSmartModalComponent;
  passwordPolicyModal: NgxSmartModalComponent;
  measurementSettingsModal: NgxSmartModalComponent;

  get idpPartnerCertificates() {
    if (!this.tenantDetails?.options?.idp?.partnerCertificates?.length) return '';

    let certificatesNames = '';
    for (let i = 0; i < this.tenantDetails.options.idp.partnerCertificates.length; i++) {
      if (!this.tenantDetails.options.idp.partnerCertificates[i].fileName) return;

      if (certificatesNames) certificatesNames += ', ';
      certificatesNames += this.tenantDetails.options.idp.partnerCertificates[i].fileName;
    }

    return certificatesNames;
  }

  constructor(
    private translateService: TranslateService,
    private hydraApi: HydraApiService,
    private ngxSmartModalService: NgxSmartModalService,
    private spinnerService: SpinnerStateService,
    private toastrService: ToastrService,
    private userState: UserStateService
  ) {}

  ngOnInit() {
    this.showDocumentLimits = this.userState.hasFeature(Feature.Capture);
    this.showMeasurementSettings = this.userState.hasFeature(Feature.OLCTED);
    this.hydraApi.getTenantDetails(this.tenantDetailsLoaded, this.tenantDetailsLoadFailed);
  }

  ngAfterViewInit() {
    this.authenticationModal = this.ngxSmartModalService.getModal('tenantoptionsauthentication');
    this.documentLimitsModal = this.ngxSmartModalService.getModal('tenantoptionsdocumentlimits');
    this.passwordPolicyModal = this.ngxSmartModalService.getModal('tenantoptionspasswordpolicy');
    this.measurementSettingsModal = this.ngxSmartModalService.getModal('tenantoptionsmeasurementsettings');
  }

  tenantDetailsLoaded = (td: TenantDetails): void => {
    this.tenantDetails = td;
    this.updateExpireUnits();
  };

  tenantDetailsLoadFailed = () => {
    this.toastrService.error(this.translateService.instant('admin-tenant-options.failed-to-load-tenant-details'));
  };

  updateExpireUnits = (): void => {
    this.expireUnits = this.tenantDetails.passwordPolicy.expireUnits;
    if (this.tenantDetails.passwordPolicy.expiresAfter == 1) {
      if (this.expireUnits == 0) this.expireUnits = 4;
      else if (this.expireUnits == 1) this.expireUnits = 5;
      else if (this.expireUnits == 2) this.expireUnits = 6;
      else if (this.expireUnits == 3) this.expireUnits = 7;
    }
  };

  //////////////// Authentication Options /////////////

  onEditAuthentication = () => {
    this.ngxSmartModalService.setModalData(
      {
        options: new TenantOptions(this.tenantDetails.options),
      },
      'tenantoptionsauthentication',
      true
    );

    this.authenticationModal.open();
  };

  saveAuthentication = () => {
    const data = this.ngxSmartModalService.getModalData('tenantoptionsauthentication') as any;

    this.hydraApi.updateTenantOptions(data.options, this.authenticationUpdated, this.authenticationUpdateFailed);

    this.spinnerService.show(
      this.translateService.instant('admin-tenant-options.saving'),
      document.getElementById('app-spinner-panel')
    );
  };

  authenticationUpdated = (result: ApiResult) => {
    this.spinnerService.hide();

    if (result.success) {
      this.tenantDetails.options = (
        this.ngxSmartModalService.getModalData('tenantoptionsauthentication') as any
      ).options;
      this.userState.userProfile.tenantOptions.authenticationType = this.tenantDetails.options.authenticationType;

      this.authenticationModal.close();
      this.toastrService.success(this.translateService.instant('admin-tenant-options.save-succeeded'));
    } else {
      result.errors.forEach((error) => {
        this.optionsErrors.push(error);
      });
    }
  };

  authenticationUpdateFailed = () => {
    this.spinnerService.hide();
    this.optionsErrors.push(this.translateService.instant('admin-tenant-options.save-failed'));
  };

  authenticationChanged = (checkFieldChanged: boolean) => {
    this.optionsErrors = [];
  };

  onAuthenticationModalClosed = (): void => {
    this.optionsErrors = [];
  };

  cancelAuthentication = () => {
    this.authenticationModal.close();
  };

  //////////////// Document Limits /////////////

  onEditDocumentLimits = () => {
    this.ngxSmartModalService.setModalData(
      {
        documentLimits: new DocumentLimits(this.tenantDetails.documentLimits),
      },
      'tenantoptionsdocumentlimits',
      true
    );
    this.tenantoptionsdocumentlimitsModalClosed = false;
    this.documentLimitsModal.open();
  };

  saveDocumentLimits = () => {
    const data = this.ngxSmartModalService.getModalData('tenantoptionsdocumentlimits') as any;

    this.hydraApi.updateDocumentLimits(
      data.documentLimits,
      this.documentLimitsUpdated,
      this.documentLimitsUpdateFailed
    );

    this.spinnerService.show(
      this.translateService.instant('admin-tenant-options.document-limits.saving'),
      document.getElementById('app-spinner-panel')
    );
  };

  documentLimitsUpdated = (result: ApiResult) => {
    this.spinnerService.hide();
    if (result.success) {
      this.tenantDetails.documentLimits = (
        this.ngxSmartModalService.getModalData('tenantoptionsdocumentlimits') as any
      ).documentLimits;

      this.documentLimitsModal.close();
      this.toastrService.success(this.translateService.instant('admin-tenant-options.document-limits.save-succeeded'));
    } else {
      result.errors.forEach((error) => {
        this.documentLimitsErrors.push(error);
      });
    }
  };

  documentLimitsUpdateFailed = () => {
    this.passwordPolicyErrors = [];
    this.spinnerService.hide();
    this.documentLimitsErrors.push(this.translateService.instant('admin-tenant-options.document-limits.save-failed'));
  };

  documentLimitsChanged = (checkFieldChanged: boolean) => {
    this.documentLimitsErrors = [];
  };

  onDocumentLimitModalClosed = (): void => {
    this.documentLimitsErrors = [];
  };

  cancelDocumentLimits = () => {
    this.tenantoptionsdocumentlimitsModalClosed = true;
    this.documentLimitsModal.close();
  };

  //////////////// Passowrd Policy /////////////

  onEditMeasurementSettings = () => {
    this.ngxSmartModalService.setModalData(
      {
        measurementSettings: new MeasurementSettings(this.tenantDetails.tedOptions.measurementSettings),
      },
      'tenantoptionsmeasurementsettings',
      true
    );
    this.measurementSettingsModal.open();
  };

  savePasswordPolicy = () => {
    this.passwordPolicyErrors = [];
    const data = this.ngxSmartModalService.getModalData('tenantoptionspasswordpolicy') as any;
    this.hydraApi.updatePasswordPolicy(
      data.passwordPolicy,
      this.passwordPolicyUpdated,
      this.passwordPolicyUpdateFailed
    );

    this.spinnerService.show(
      this.translateService.instant('admin-tenant-options.password-policy.saving'),
      document.getElementById('app-spinner-panel')
    );
  };

  passwordPolicyUpdated = (result: ApiResult) => {
    this.passwordPolicyErrors = [];
    this.spinnerService.hide();
    if (result.success) {
      this.tenantDetails.passwordPolicy = (
        this.ngxSmartModalService.getModalData('tenantoptionspasswordpolicy') as any
      ).passwordPolicy;

      this.passwordPolicyModal.close();
      this.toastrService.success(this.translateService.instant('admin-tenant-options.password-policy.save-succeeded'));
      this.updateExpireUnits();
    } else {
      result.errors.forEach((error) => {
        this.passwordPolicyErrors.push(error);
      });
    }
  };

  passwordPolicyUpdateFailed = () => {
    this.spinnerService.hide();
    this.passwordPolicyErrors.push(this.translateService.instant('admin-tenant-options.password-policy.save-failed'));
  };

  passwordPolicyChanged = (checkFieldChanged: boolean) => {
    this.passwordPolicyErrors = [];
  };

  onPasswordPolicyModalClosed = (): void => {
    this.passwordPolicyErrors = [];
  };

  cancelPasswordPolicy = () => {
    this.passwordPolicyModal.close();
  };

  //////////////// Measurement Settings /////////////

  onEditPasswordPolicy = () => {
    this.ngxSmartModalService.setModalData(
      {
        passwordPolicy: new PasswordPolicy(this.tenantDetails.passwordPolicy),
      },
      'tenantoptionspasswordpolicy',
      true
    );

    this.passwordPolicyModal.open();
  };

  saveMeasurementSettings = () => {
    const data = this.ngxSmartModalService.getModalData('tenantoptionsmeasurementsettings') as any;

    this.userState.updateOlcUserDefaultUnit(
      data.measurementSettings,
      this.measurementSettingsUpdated,
      this.measurementSettingsUpdateFailed
    );

    this.spinnerService.show(
      this.translateService.instant('admin-tenant-options.measurement-settings.saving'),
      document.getElementById('app-spinner-panel')
    );
  };

  measurementSettingsUpdated = (result: ApiResult) => {
    this.spinnerService.hide();

    if (result.success) {
      this.tenantDetails.tedOptions.measurementSettings = (
        this.ngxSmartModalService.getModalData('tenantoptionsmeasurementsettings') as any
      ).measurementSettings;

      this.measurementSettingsModal.close();
      this.toastrService.success(
        this.translateService.instant('admin-tenant-options.measurement-settings.save-succeeded')
      );
    } else {
      result.errors.forEach((error) => {
        this.measurementSettingsErrors.push(error);
      });
    }
  };

  measurementSettingsUpdateFailed = () => {
    this.spinnerService.hide();
    this.measurementSettingsErrors.push(
      this.translateService.instant('admin-tenant-options.measurement-settings.save-failed')
    );
  };

  measurementSettingsChanged = () => {
    this.measurementSettingsErrors = [];
  };

  onMeasurementSettingsModalClosed = (): void => {
    this.measurementSettingsErrors = [];
  };

  cancelMeasurementSettings = () => {
    this.measurementSettingsModal.close();
  };
}
