import { Component, OnInit, ViewEncapsulation, Input, Output, EventEmitter } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  FieldDefinition,
  MetaDataType,
  PasswordPolicy,
  ExpirationUnits,
  IFieldOptions,
  FixedDataSource,
} from 'shared-code-lib';

@Component({
  selector: 'app-admin-tenant-options-password-policy',
  templateUrl: './admin-tenant-options-password-policy.component.html',
  styleUrls: ['./admin-tenant-options-password-policy.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminTenantOptionsPasswordPolicyComponent implements OnInit {
  @Input() passwordPolicy: PasswordPolicy;
  @Output() fieldChanged = new EventEmitter<boolean>();
  expirationUnitsDataSource: FixedDataSource;
  expirationUnits: FieldDefinition;
  expireAfter: FieldDefinition;
  passwordMemoryCount: FieldDefinition;
  minimumLength: FieldDefinition;
  minimumNumbers: FieldDefinition;
  minimumSpecialCharacters: FieldDefinition;
  minimumUpperCaseCharacters: FieldDefinition;
  minimumLowerCaseCharacters: FieldDefinition;

  get passwordPolicyExpiresAfter() {
    return `${this.passwordPolicy.expiresAfter}`;
  }
  set passwordPolicyExpiresAfter(val: string) {
    this.passwordPolicy.expiresAfter = Number(val);
  }
  get passwordPolicyExpireUnits() {
    return `${this.passwordPolicy.expireUnits}`;
  }
  set passwordPolicyExpireUnits(val: string) {
    this.passwordPolicy.expireUnits = Number(val);
  }
  get passwordPolicyPasswordMemoryCount() {
    return `${this.passwordPolicy.passwordMemoryCount}`;
  }
  set passwordPolicyPasswordMemoryCount(val: string) {
    this.passwordPolicy.passwordMemoryCount = Number(val);
  }
  get passwordPolicyMinimumLength() {
    return `${this.passwordPolicy.minimumLength}`;
  }
  set passwordPolicyMinimumLength(val: string) {
    this.passwordPolicy.minimumLength = Number(val);
  }
  get passwordPolicyMinimumNumbers() {
    return `${this.passwordPolicy.minimumNumbers}`;
  }
  set passwordPolicyMinimumNumbers(val: string) {
    this.passwordPolicy.minimumNumbers = Number(val);
  }
  get passwordPolicyMinimumSpecialCharacters() {
    return `${this.passwordPolicy.minimumSpecialCharacters}`;
  }
  set passwordPolicyMinimumSpecialCharacters(val: string) {
    this.passwordPolicy.minimumSpecialCharacters = Number(val);
  }
  get passwordPolicyMinimumUpperCaseCharacters() {
    return `${this.passwordPolicy.minimumUpperCaseCharacters}`;
  }
  set passwordPolicyMinimumUpperCaseCharacters(val: string) {
    this.passwordPolicy.minimumUpperCaseCharacters = Number(val);
  }
  get passwordPolicyMinimumLowerCaseCharacters() {
    return `${this.passwordPolicy.minimumLowerCaseCharacters}`;
  }
  set passwordPolicyMinimumLowerCaseCharacters(val: string) {
    this.passwordPolicy.minimumLowerCaseCharacters = Number(val);
  }

  constructor(private translateService: TranslateService) {}

  ngOnInit() {
    this.createFields();
  }

  createFields = (): void => {
    this.expireAfter = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-tenant-options.password-policy.expire-after'),
      placeholder: this.translateService.instant('admin-tenant-options.password-policy.expire-after-placeholder'),
      required: true,
    });

    this.passwordMemoryCount = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-tenant-options.password-policy.password-memory-count'),
      placeholder: this.translateService.instant(
        'admin-tenant-options.password-policy.password-memory-count-placeholder'
      ),
      required: true,
    });

    this.minimumLength = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-tenant-options.password-policy.min-length'),
      placeholder: this.translateService.instant('admin-tenant-options.password-policy.min-length-placeholder'),
      required: true,
    });

    this.minimumNumbers = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-tenant-options.password-policy.min-numbers'),
      placeholder: this.translateService.instant('admin-tenant-options.password-policy.min-numbers-placeholder'),
      required: true,
    });

    this.minimumSpecialCharacters = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-tenant-options.password-policy.min-special-characters'),
      placeholder: this.translateService.instant(
        'admin-tenant-options.password-policy.min-special-characters-placeholder'
      ),
      required: true,
    });

    this.minimumUpperCaseCharacters = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-tenant-options.password-policy.min-upper-case-characters'),
      placeholder: this.translateService.instant(
        'admin-tenant-options.password-policy.min-upper-case-characters-placeholder'
      ),
      required: true,
    });

    this.minimumLowerCaseCharacters = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-tenant-options.password-policy.min-lower-case-characters'),
      placeholder: this.translateService.instant(
        'admin-tenant-options.password-policy.min-lower-case-characters-placeholder'
      ),
      required: true,
    });

    const expirationUnits = new Array<IFieldOptions>();

    expirationUnits.push({
      name: this.translateService.instant('admin-tenant-options.password-policy.unit-' + ExpirationUnits.Day),
      value: `${ExpirationUnits.Day}`,
    });
    expirationUnits.push({
      name: this.translateService.instant('admin-tenant-options.password-policy.unit-' + ExpirationUnits.Week),
      value: `${ExpirationUnits.Week}`,
    });
    expirationUnits.push({
      name: this.translateService.instant('admin-tenant-options.password-policy.unit-' + ExpirationUnits.Month),
      value: `${ExpirationUnits.Month}`,
    });
    expirationUnits.push({
      name: this.translateService.instant('admin-tenant-options.password-policy.unit-' + ExpirationUnits.Year),
      value: `${ExpirationUnits.Year}`,
    });

    this.expirationUnitsDataSource = new FixedDataSource(expirationUnits);
    this.expirationUnits = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-tenant-options.password-policy.expiration-unit'),
      placeholder: this.translateService.instant('admin-tenant-options.password-policy.expiration-unit-placeholder'),
      required: true,
      datasource: this.expirationUnitsDataSource,
    });
  };

  tenantFieldChanged = (): void => {
    this.fieldChanged.emit(true);
  };
}
