import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { EnumDataSource, FieldDefinition, MeasurementSettings, MeasurementUnit, MetaDataType } from 'shared-code-lib';

@Component({
  selector: 'app-admin-tenant-options-measurement-settings',
  templateUrl: './admin-tenant-options-measurement-settings.component.html',
  styleUrls: ['./admin-tenant-options-measurement-settings.component.scss'],
})
export class AdminTenantOptionsMeasurementSettingsComponent implements OnInit {
  @Input() measurementSettings: MeasurementSettings;
  @Output() fieldChanged = new EventEmitter<boolean>();

  fieldUnit: FieldDefinition;

  get measurementSettingsUnit() {
    return `${this.measurementSettings.unit}`;
  }
  set measurementSettingsUnit(val: string) {
    this.measurementSettings.unit = Number(val);
  }

  constructor(private translateService: TranslateService) {}

  ngOnInit() {
    this.createFields();
  }

  createFields = (): void => {
    this.fieldUnit = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-tenant-options.measurement-settings.unit'),
      required: true,
      datasource: new EnumDataSource(
        MeasurementUnit,
        'admin-tenant-options.measurement-settings.unit-enum',
        this.translateService
      ),
    });
  };

  tenantFieldChanged = (): void => {
    this.fieldChanged.emit(true);
  };
}
