import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminTenantOptionsComponent } from './admin-tenant-options.component';
import { AdminTenantOptionsAuthenticationComponent } from './admin-tenant-options-authentication/admin-tenant-options-authentication.component';
import { AdminTenantOptionsDocumentLimitsComponent } from './admin-tenant-options-document-limits/admin-tenant-options-document-limits.component';
import { AdminTenantOptionsPasswordPolicyComponent } from './admin-tenant-options-password-policy/admin-tenant-options-password-policy.component';
import { AdminTenantOptionsMeasurementSettingsComponent } from './admin-tenant-options-measurement-settings/admin-tenant-options-measurement-settings.component';

const routes: Routes = [
  {
    path: '',
    component: AdminTenantOptionsComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminTenantOptionsRoutingModule {}

export const routedComponents = [
  AdminTenantOptionsComponent,
  AdminTenantOptionsAuthenticationComponent,
  AdminTenantOptionsDocumentLimitsComponent,
  AdminTenantOptionsPasswordPolicyComponent,
  AdminTenantOptionsMeasurementSettingsComponent,
];
