import { Component, OnInit, ViewEncapsulation, Input, Output, EventEmitter } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { DocumentLimits, FieldDefinition, MetaDataType } from 'shared-code-lib';

@Component({
  selector: 'app-admin-tenant-options-document-limits',
  templateUrl: './admin-tenant-options-document-limits.component.html',
  styleUrls: ['./admin-tenant-options-document-limits.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminTenantOptionsDocumentLimitsComponent implements OnInit {
  @Input() documentLimits: DocumentLimits;
  @Output() fieldChanged = new EventEmitter<boolean>();

  fieldType: FieldDefinition;

  perMonth: FieldDefinition;
  perDocument: FieldDefinition;

  get monthlyPageLimit() {
    return `${this.documentLimits.monthlyPageLimit}`;
  }
  set monthlyPageLimit(val: string) {
    this.documentLimits.monthlyPageLimit = Number(val);
  }
  get documentPageLimit() {
    return `${this.documentLimits.documentPageLimit}`;
  }
  set documentPageLimit(val: string) {
    this.documentLimits.documentPageLimit = Number(val);
  }

  constructor(private translateService: TranslateService) {}

  ngOnInit() {
    this.createFields();
  }

  createFields = (): void => {
    this.perMonth = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-tenant-options.document-limits.total-per-month'),
      placeholder: this.translateService.instant('admin-tenant-options.document-limits.total-per-month-placeholder'),
      required: true,
    });

    this.perDocument = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-tenant-options.document-limits.total-per-document'),
      placeholder: this.translateService.instant('admin-tenant-options.document-limits.total-per-document-placeholder'),
      required: true,
    });
  };

  tenantFieldChanged = (): void => {
    this.fieldChanged.emit(true);
  };
}
